
.navbar {
  background-color: #007bff;
  padding: 20px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.navbar .navbar-brand {
  margin-right: 20px;
  font-size: 24px;
  font-weight: bold;
  color: #fff;
}

.navbar .navbar-nav {
  display: flex;
}

.navbar .navbar-nav .nav-link{
  color: #fff !important;
  font-size: 18px;
  padding: 10px 15px;
  transition: color 0.3s ease;
}

.navbar .navbar-nav .nav-link:hover {
  color: #ff5100 !important;
}

.search-container {
  display: flex;
  align-items: center;
  gap: 4px;
}

.search-input {
  padding: 5px 10px;
  border: 1px solid black ;
  border-radius: 4px;
}

.search-button {
  padding: 5px 10px;
  background-color: #5299e5;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.search-button:hover {
  background-color: #0056b3;
}
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
  }

  .search-container {
    width: 100%; 
    margin-top: 10px;
  }

  .navbar .navbar-nav {
    flex-direction: column;
    margin-bottom: 10px;
  }
}
